:root {
  --gold-web-golden: #f9d624;
  --spanish-orange: #e65c00;
  --persian-green: #11978c;
  --granite-gray: #666;
  --spring-green: #39ef7f;
  --ultramarine: #4b00e0;
  --blue-violet: #8d2ce2;
  --smoky-black: #121212;
  --dark-orange: #ff8c00;
  --winter-sky: #ff057a;
  --cultured-1: #fafafa;
  --white-1: #fff;
  --black: #000;
  --ff-spartan: "League Spartan", sans-serif;
  --fs-1: 3rem;
  --fs-2: 2.7rem;
  --fs-3: 2.2rem;
  --fs-4: 1.8rem;
  --fw-700: 700;
  --fw-900: 900;
  --section-padding: 60px;
  --radius-6: 6px;
  --gradient-1: linear-gradient(to right, var(--winter-sky), var(--dark-orange));
  --transition-1: .25s ease;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--white-1);
  color: var(--granite-gray);
  font-family: var(--ff-spartan);
  font-size: 1.6rem;
}

h1, h2, h3 {
  color: var(--black);
  font-weight: var(--fw-900);
  line-height: 1.3;
}

h1 {
  font-size: var(--fs-1);
}

h2 {
  font-size: var(--fs-2);
}

h3 {
  font-size: var(--fs-3);
  font-weight: var(--fw-700);
}

.container {
  padding-inline: 15px;
}

.section {
  padding-block: var(--section-padding);
}

.btn {
  min-height: 50px;
  color: var(--white-1);
  border-radius: var(--radius-6);
  padding-inline: 25px;
}

.btn-primary {
  background-image: var(--gradient-1);
}

.header {
  background-color: var(--white-1);
  z-index: 4;
  width: 100%;
  padding-block: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.logo {
  color: var(--smoky-black);
  font-size: 3.5rem;
  font-weight: var(--fw-700);
  line-height: 1;
}

.hero {
  background-color: var(--cultured-1);
  padding-block-start: calc(73px + var(--section-padding));
}

.hero-title {
  margin-block: 30px 15px;
}

@media (width >= 768px) {
  body {
    font-size: 2rem;
  }

  :root {
    --fs-1: 5rem;
    --fs-2: 3.5rem;
    --fs-3: 2.5rem;
  }

  .container {
    max-width: 720px;
  }
}

@media (width >= 1200px) {
  :root {
    --fs-1: 6rem;
    --fs-2: 5rem;
  }

  .container {
    max-width: 1140px;
  }
}
